// Main entry point for better-markdown library
// Export all markdown-it plugins for use in any editor (CodeMirror, Monaco, etc.)

// Import all plugins
import customSyntaxPlugin from './plugins/customSyntax.js';
import markdownCheckboxes from './plugins/checkboxes.js';
import markdownRadios from './plugins/radios.js';
import markdownCallouts from './plugins/callouts.js';
import markdownToggles from './plugins/toggles.js';
import markdownWikilinks from './plugins/wikilinks.js';
import markdownBlockIds from './plugins/blockIds.js';

// Import utilities
import * as wikilinkUtils from './utils/wikilinks.js';

// Named exports for individual plugins
export {
    customSyntaxPlugin,
    markdownCheckboxes,
    markdownRadios,
    markdownCallouts,
    markdownToggles,
    markdownWikilinks,
    markdownBlockIds,
    wikilinkUtils
};

/**
 * Convenience function to apply all plugins to a markdown-it instance
 * @param {MarkdownIt} md - The markdown-it instance
 * @returns {MarkdownIt} The same instance with all plugins applied
 * 
 * @example
 * import MarkdownIt from 'markdown-it';
 * import { applyAllPlugins } from 'better-markdown';
 * 
 * const md = new MarkdownIt();
 * applyAllPlugins(md);
 */
export function applyAllPlugins(md) {
    md.use(customSyntaxPlugin);
    md.use(markdownCheckboxes);
    md.use(markdownRadios);
    md.use(markdownCallouts);
    md.use(markdownToggles);
    md.use(markdownWikilinks);
    md.use(markdownBlockIds);
    return md;
}

// Default export with all plugins and utilities
export default {
    customSyntaxPlugin,
    markdownCheckboxes,
    markdownRadios,
    markdownCallouts,
    markdownToggles,
    markdownWikilinks,
    markdownBlockIds,
    wikilinkUtils,
    applyAllPlugins
};
